import Vue from "vue";
import { extend, ValidationObserver, ValidationProvider } from "vee-validate";
import { min, max, required, numeric, digits, email, confirmed } from "vee-validate/dist/rules";
extend('password', {
    validate: value => {
        return /^.*(?=.*[a-zA-Z])(?=.*[0-9])(?=.*[0-11])(?=.*[$@$!%*#?&]).*$/.test(value)
    },
    message: ' 10  \n,, .'
});
extend("max", {
    ...max,
    message: "  {length}  ."
});
extend("required", {
    ...required,
    message: "{_field_}   ."
});
extend("numeric", {
    ...numeric,
    message: "{_field_}    ."
});
extend("digits", {
    ...digits,
    message: "{_field_}  {length} ."
});
extend("email", {
    ...email,
    message: "   ."
});
extend("confirmed", {
    ...confirmed,
    message: "    ."
})
extend("min", {
    ...min,
    message: " {length}  ."
})
components: {
    ValidationObserver,
    ValidationProvider;
}
Vue.component("ValidationObserver", ValidationObserver);
Vue.component("ValidationProvider", ValidationProvider);